export default {
  name: 'Case',
  props: {
    caseList: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    navigateToDetail (item) {
      let routeUrl = this.$router.resolve({
        name: 'newsCase-id',
        params: {
          id: item.case_id
        },
        query: {
          type: 'case'
        }
      })
      window.open(routeUrl.href, '_blank')
    }
  }
}
